.header {
  color: white;
  display: flex;
  padding: 20px;
  /* padding-left: 10px; */
  align-items: center;
  justify-content: space-between;
  overflow-x: scroll;
}

/* Hiding Scrollbar */
/* Hide scrollbar for Chrome, Safari and Opera */
.header::-webkit-scrollbar {
  display: none;
}
/* Hide scrollbar for IE, Edge and Firefox */
.header {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}



.header_icon > .MuiSvgIcon-root {
  font-size: 30px;
  margin-left: auto;
  margin-right: auto;
}

.header_icon--active > p {
  display: inline !important;
}

.header_icon:hover > p {
  display: inline;
}

.header_icon > p {
  display: none;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-left: auto;
  margin-right: auto;
  font-size: small;
}

.header_icon {
  width: 100px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  align-items: center;
  margin-right: 30px;
}

.header_icons {
  display: flex;
  align-items: center;
  text-align: center;
}

.header > img {
  object-fit: contain;
  height: 80px;
}
